<template>
  <input
    class="input__phone"
    v-if="getTypeInput === 'phone' || getTypeInput === 'whts'"
    type="text"
    v-phone-mask
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
  />

  <input
    class="input__phone"
    v-else-if="getTypeInput === 'card'"
    type="text"
    v-card-number
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
  />

  <input
    class="input__phone"
    v-else-if="!getTypeInput"
    type="text"
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
  />

  <input
    class="input__phone"
    v-else-if="getTypeInput === 'date'"
    type="text"
    v-date
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
  />
</template>

<script lang="ts" setup>
import { vPhoneMask } from "@/directives/ValidationPhoneDirective";
import { vFocusClass } from "@/directives/FocusDirective";
import { vCardNumber } from "@/directives/CardNumberDirective";
import { vDate } from "@/directives/DateDerictive";
import { defineProps, computed, ref, defineEmits, onMounted, watch } from "vue";

const emit = defineEmits(["input"]);

//объект в InputNumberInterface
const props = defineProps({
  value: {
    type: Object,
  },
});

const numberValue = ref("");

const MAX_LENGTH_PHONE = 11;

const getTypeInput = computed(() => {
  if (props.value) {
    return props.value.type;
  }
});

const getPlaceholderInput = computed(() => {
  if (props.value) {
    return props.value.placeholder || "число";
  }
});

function applyPhoneMask(value: string): string {
  const matrix = "+7 (___) ___-__-__";
  const def = matrix.replace(/\D/g, "");
  const val = value.replace(/\D/g, "");

  let i = 0;
  const newValue = matrix.replace(/[_\d]/g, (a) => {
    return i < val.length ? val.charAt(i++) || def.charAt(i) : a;
  });

  i = newValue.indexOf("_");
  if (i !== -1) {
    i < 5 && (i = 3);
    return newValue.slice(0, i);
  }

  return newValue;
}

const setNewValue = () => {
  const digits = numberValue.value
    .replace(/\D/g, "")
    .slice(0, MAX_LENGTH_PHONE);
  emit("input", digits);
};

onMounted(() => {
  if (props.value && props.value !== null) {
    if (props.value.type === "phone" || props.value.type === "whts") {
      const maskedValue = applyPhoneMask(String(props.value.value));
      numberValue.value = maskedValue;
    } else {
      numberValue.value = props.value.value;
    }
  }
});

watch(
  () => props.value,
  (newValue) => {
    if (newValue !== null) {
      const maskedValue = applyPhoneMask(String(newValue));
      numberValue.value = maskedValue;
    }
  }
);
</script>

<style scoped lang="scss">
@import url(../../styles/variable.css);

input[type="text"] {
  caret-color: var(--yellow);
}
</style>

<!-- какое то расширение ругается, что отсутвует export default -->
<script lang="ts">
export default {};
</script>
